@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.radio{
    border: 1px solid #eee8e8;
    border-radius: 10px;
    /* background-color: black; */
  }
  
  
  .stations {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-wrap: nowrap;
              flex-wrap: nowrap;
      overflow-x: auto;
    }
  
     .stations::-webkit-scrollbar{
      margin-top: 10px;
    } 
  
    
  .station {
    font-size: 1rem;
    border: 1px solid rgb(48, 46, 49);
    margin: 0.35em;
    padding: 12px;
    display: -webkit-flex;
    display: flex;
    border-radius: 2px;
   
    grid-gap: 10px;
   
    gap: 10px;
    -webkit-align-items: center;
            align-items: center;
    
  }
  
  .station:hover {
    border-color: #fc0000;
    
  }
  
  audio {
    width: 100%;
  }
  
  .name {
     background-color: white;
      text-align: center;
      font-size: small;
     
    }
  
  .stationName img {
    margin: 0;
  }
  
  
  .rhap_container.player {
      background-color:transparent;
      display: -webkit-flex;
      display: flex;
      justify-items: center;
  
      border-radius: 10px;
      width: 200px;
    }
    
    .rhap_stacked .rhap_controls-section {
      margin-top: 0 !important;
  
    }
    
    .rhap_controls-section .rhap_volume-controls {
      -webkit-justify-content: center;
              justify-content: center;
    }
    
.Recipe__container{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 40px 24px;
}
.Recipe__Header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.Recipe__Form{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 18px;
}
.Recipe__Form__textfield{
    padding :6px 16px ;
    min-width: 280px;
    max-width: 100vw;
    font-size: 16px;
    outline: 1px solid black ;
    border-radius: 2px;
}
.Recipe__Form__textfield:focus{
    outline: 1px solid rgb(60, 115, 218);


}
.Recipe__submitBtn{
    padding :6px 16px ;
    cursor: pointer;
    margin: 0 8px;
    border-radius: 4px;
    background-color: black;
    color: white;
    transition: 0.3s;
}
.Recipe__submitBtn:hover{
    background-color: rgb(59, 59, 59);
}
.Recipe_recipescontainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top:10px;
}
.Recipe_recipesGrid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .Recipe_recipesGrid{
        display: grid;
        grid-template-columns: 1fr ;
        
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 768px) {
    .Recipe_recipesGrid{
    display: grid;
    grid-template-columns: 1fr 1fr ;
    
}}

.RecipeTile{
    margin: 6px 8px;
    border: 1px solid rgb(100, 100, 100);
    padding: 5px;
    border-radius: 3px;
    box-shadow: 3px 2px 2px rgb(148, 148, 148);
    cursor:pointer;
}
.RecipeTile>img{
    object-fit:contain;
}
.RecipeTile__label{
    text-align: center;
}
.navbar{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 12px 24px;
    background-color: rgb(24, 21, 21);
    color: white;
}
.userPic{
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.RecipeInfo {
  /* display:flex;
    justify-content: center;
    align-items: center;
    max-width: 80vw;
    min-width: 30vw; */
  display: block;
  max-width: 80%;
  max-height:100%;
  margin: 30px auto;
  box-shadow: rgba(0, 0, 0, 0.5);
  border: 3px solid white;
  background-color: white;
  overflow:auto ;
  bottom: 20px;
  padding: 4px 8px;
  border-radius: 5px;
}
.RecipeInfo_Heading{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.RecipeInfo__HealthTags {
  background-color: rgb(109, 109, 109);
  padding: 2px 6px;
  color: white;
  border-radius: 3px;
  margin: 4px;
  font-size: 12px;
  line-break: strict;
  word-break: keep-all;
  display: inline-block;
}

.RecipeInfo__ImageInstruction{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}
.RecipeInfo__ImageInstruction  h1{
    font-weight: 500;
}
.For_More{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}
.For_More_link{
    background-color: black;
    color: white;
    text-decoration: none;
    padding: 4px 6px;
    border-radius: 3px ;
}
